import React from "react";
import {
  EditBase,
  TextInput,
  ReferenceInput,
  AutocompleteInput,
  BooleanInput,
  FormWithRedirect,
  Toolbar,
  useEditContext,
} from "react-admin";
import { Card, CardContent, Divider, Box } from "@material-ui/core";

import { Avatar } from "./Avatar";
import { ContactAside } from "./ContactAside";

const Spacer = () => <Box width={20} component="span" />;

const ContactEditContent = () => {
  const { record, loaded } = useEditContext();
  if (!loaded || !record) return null;
  return (
    <Box mt={2} display="flex">
      <Box flex="1">
        <FormWithRedirect
          record={record}
          redirect="show"
          render={(formProps) => (
            <Card>
              <CardContent>
                <Box>
                  <Box display="flex">
                    <Box mr={2}>
                      <Avatar record={record} />
                    </Box>
                    <Box flex="1" mt={-1}>
                      <Box display="flex">
                        <TextInput source="first_name" />
                        <Spacer />
                        <TextInput source="last_name" />
                      </Box>
                      <Box display="flex">
                        <TextInput source="title" />
                        <Spacer />
                        <ReferenceInput
                          source="company_id"
                          reference="companies"
                        >
                          <AutocompleteInput optionText="name" />
                        </ReferenceInput>
                      </Box>
                      <Divider />
                      <Box mt={2} width={430}>
                        <TextInput source="email" fullWidth />
                      </Box>
                      <Box display="flex">
                        <TextInput source="phone_number1" />
                        <Spacer />
                        <TextInput source="phone_number2" />
                      </Box>
                      <Divider />
                      <Box mt={2} width={430}>
                        <TextInput source="background" multiline fullWidth />
                        <TextInput source="avatar" fullWidth />
                        <BooleanInput source="has_newsletter" />
                      </Box>
                    </Box>
                  </Box>
                </Box>
              </CardContent>
              <Toolbar {...formProps} />
            </Card>
          )}
        />
      </Box>
      <ContactAside record={record} link="show" />
    </Box>
  );
};

export const ContactEdit = (props) => (
  <EditBase {...props}>
    <ContactEditContent />
  </EditBase>
);
